    <!--      <v-spacer />-->
    <!--    </v-app-bar>-->
    <v-navigation-drawer v-model="drawer" app clipped>
      <v-list>
        <v-list-item-group v-model="active">
          <v-list-item
            v-for="item in menuList"
            :key="item.path"
            :value="item.path"
            :to="item.path"
            color="primary"
          >
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
